body {
  width: 100%;
  overflow-x: hidden;
  max-width: 100%;
  font: 14px Roboto, sans-serif;
  line-height: 1.429;
}

.container {
  max-width: 1110px;
  width: 1100px;
  margin: 0 auto;
}

#trapezoid {
  border-bottom: 100px solid red;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  height: 0;
  width: 100px;
}

.progress {
  max-width: 520px;
  width: 100%;
}

.progress__bar {
  position: relative;
  border: 3px solid blue;
  display: flex;
  width: 100%;
  height: 30px;
  border-radius: 30px;
}

.progress__bar-left {
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  right: calc(50% + 14.5px);
  background-color: blue;
  border-width: 0 0 20px 0;
  border-radius: 30px 0 0 30px;
}

.progress__bar-left:after {
  position: absolute;
  right: -28px;
  bottom: 0;
  content: '';
  width: 0;
  height: 0;
  border-bottom: 29px solid blue;
  border-right: 29px solid transparent;
  z-index: 10;
}

.progress__bar-right {
  position: absolute;
  top: -2px;
  bottom: -2px;
  right: -2px;
  left: calc(50% - 14.5px);
  background: linear-gradient(90deg, rgba(0, 0, 255, 0.6), white);
  border-radius: 0 30px 30px 0;
}

body {
  background-color: #000;
}

.footer {
  height: 400px;
  background: linear-gradient(to bottom, #000, #eee 85%, #000 100%);
}

.footer__img {
  width: 30px;
  height: 40px;
  background-color: yellow;
  box-shadow: 0 1px 5px 5px rgba(255, 255, 0, 0.7);
  border-radius: 20px;
  position: absolute;
  z-index: 10;
}

.footer__img_l {
  top: 70px;
  left: -247px;
}

.footer__img_r {
  top: 0;
  left: 110px;
}

.footer__wrap {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  max-width: 1520px;
  margin: 0 auto;
  height: 300px;
  padding: 0 20px;
}

.footer__left {
  margin: 0 60px 0 0;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  flex-wrap: wrap;
}

.footer__left-item {
  position: relative;
  width: 100%;
  background-color: black;
}

.footer__left-text {
  color: #fff;
  z-index: 10;
}

.footer__left-num {
  position: absolute;
  top: 0;
  right: 20px;
  color: #000000;
  font-size: 48px;
  line-height: 0.8;
  color: rgba(255, 255, 0, 0.5);
  z-index: 1;
}

.footer__center {
  width: 700px;
  background-color: blue;
  height: 300px;
}

.form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.form__item {
  width: 40%;
  height: 60px;
  padding: 10px 15px;
}

.form__item::placeholder {
  font-size: 22px;
  color: darkblue;
}

.form__item_check {
  width: 50%;
  display: flex;
  padding: 20px;
}

.form__btn {
  width: 150px;
  background-color: deepskyblue;
  margin: 0 auto;
  height: 70px;
  padding: 10px 15px;
  color: #ffffff;
  font-size: 20px;
}

.header {
  width: 100%;
  background-color: #fff;
}

.header__container {
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
}

.header__top {
  background-color: #EFEFEF;
  display: flex;
  justify-content: center;
}

.header__top-item {
  display: flex;
  justify-content: center;
}

.header__mid {
  border-bottom: 1px solid #EFEFEF;
}

.header__mid-item {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.header__banner {
  display: flex;
  align-items: center;
  background-image: url("/img/bg.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  min-height: 604px;
  justify-items: flex-start;
  align-items: center;
}

.header__banner-item {
  display: flex;
  flex-direction: column;
  max-width: 308px;
  color: #ffffff;
}

.header__banner-title {
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 56px;
  margin-bottom: 50px;
}

.header__banner-text {
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 50px;
}

.header__banner-btn {
  color: #ffffff;
  border: 2px solid #FFFFFF;
  border-radius: 40px;
  width: 170px;
  height: 40px;
  line-height: 40px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__title {
  color: #F13C6E;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: 0.1em;
  font-weight: 900;
}

.callback {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.callback__phone {
  margin-right: 3.33333rem;
}

.callback-btn {
  border-radius: 30px;
  border: 2px solid #F13C6E;
  padding: 13px 27px;
  line-height: 1.11;
}

.faq-nav {
  display: flex;
}

.faq-nav__menu {
  display: flex;
  justify-content: space-between;
}

.faq-nav__item {
  margin: 0 15px;
}

.faq-nav__link {
  line-height: 1.143;
  padding: 17px 0 16px;
  transition: all .3s ease;
}

.faq-nav__link:hover {
  color: #F13C6E;
  text-shadow: 2px #F13C6E;
}

.main-nav {
  width: 100%;
}

.main-menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-menu__item {
  display: flex;
  align-items: center;
}

.main-menu__link {
  padding: 30px 20px;
}

@media only screen and (max-width: 1200px) {
  .footer__wrap {
    flex-direction: column-reverse;
    align-items: center;
    max-width: 900px;
  }

  .footer__left {
    width: 100%;
    flex-direction: row;
    margin: 0;
  }

  .footer__left-item {
    width: calc(100%/3);
    min-width: 200px;
  }

  .footer__center {
    width: 400px;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}